<template>
  <div id="background" class="main-content">
    <div class="app flex-row align-items-center">
      <div class="container">
        <b-row class="justify-content-center mb-5">
          <b-col md="5" lg="4" xl="4">
            <div class="text-center mb-4">
              <img
                src="@/assets/logo.png"
                class="navbar-brand-full p-1"
                width="55%"
                alt="Expense Reduction Management Logo"
              />
            </div>
            <b-card-group>
              <b-card no-body class="p-1 pc-view">
                <b-card-body>
                  <b-form @submit.prevent="submit">
                    <p class="text-center text-danger">{{ loginValidation }}</p>
                    <div class="mb-1">
                      <b-form-group>
                        <b-row>
                          <b-col cols="12">
                            
                            <label class="line-3 ml-1 txtColor">USERID</label>
                          </b-col>
                          <b-col cols="12">
                            <b-input-group class="mb-1 mt-1">
                              <b-input
                                id="input-id"
                                v-model.trim="$v.login.login_id.$model"
                                type="text"
                                class="form-control bgColor sp-view"
                              />
                            </b-input-group>
                            <show-errors
                              :control="$v.login.login_id"
                              :field="$t('USERID')"
                            />
                          </b-col>
                        </b-row>
                      </b-form-group>

                      <b-form-group>
                        <b-row>
                          <b-col cols="12">
                            
                            <label class="line-3 ml-1 txtColor">
                              PASSWORD
                            </label>
                          </b-col>
                          <b-col cols="12">
                            <b-input-group class="mb-1 mt-1">
                              <b-form-input
                                id="input-pwd"
                                v-model.trim="$v.login.password.$model"
                                type="password"
                                class="form-control bgColor sp-view"
                                autocomplete="password"
                              />
                            </b-input-group>
                            <show-errors
                              :control="$v.login.password"
                              :field="$t('PASSWORD')"
                            />
                          </b-col>
                        </b-row>
                      </b-form-group>
                    </div>

                    <b-row>
                      <b-col cols="12" class="text-center mb-3">
                        <b-button
                          variant="primary"
                          type="submit"
                          class="px-4 login-btn"
                          >LOGIN</b-button
                        >
                      </b-col>
                      <b-col cols="12" class="text-center">
                        <b-link
                          v-if="!isAdmin"
                          variant="primary"
                          class="px-0"
                          to="/reset/password/request"
                        >
                          <u class="linkTxt">
                           FORGETPASSWORD
                          </u>
                        </b-link>
                        <b-link
                          v-else
                          variant="link"
                          class="px-0"
                          to="/reset/password/request"
                        >
                          <u class="linkTxt">FORGETPASSWORD</u>
                        </b-link>
                      </b-col>
                    </b-row>
                  </b-form>
                </b-card-body>
              </b-card>
            </b-card-group>
          </b-col>
        </b-row>
        <div class="justify-content-center text-center footerDispaly">
          <span class="text-center textColor">{{ $t("COPYRIGHT") }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { required } from "vuelidate/lib/validators";
import ShowErrors from "@/components/ShowErrors";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { logValuesInSession } from "@/shared/utils";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "Login",
  components: { ShowErrors },
  data() {
    return {
      login: {
        login_id: "",
        password: "",
      },
      loginValidation: "",
      access_role: "",
    };
  },
  validations: {
    login: {
      login_id: { required },
      password: { required },
    },
  },
  computed: {
    isAdmin() {
      return localStorage.getItem("role") === "ADMIN";
    },
  },
  watch: {
    login: {
      handler() {
        this.clearApiValidation();
      },
      deep: true,
    },
  },
  beforeCreate() {
    if (window.location.pathname.includes("mng")) {
      localStorage.setItem("role", "ADMIN");
      this.access_role = "ADMIN";
    } else {
      localStorage.setItem("role", "USER");
      this.access_role = "USER";
    }
  },
  methods: {
    async submit() {
      this.$v.login.$touch();
      /* If valid it will navigate to dashboard */
      if (!this.$v.login.$invalid) {
        this.loginValidation = "";
        if (localStorage.getItem("role") == "USER") {
          const { data } = await BasicInfoApis.postAPI(endPoint.LOGIN, {
            client_login_id: this.login.login_id,
            client_login_pass: this.login.password,
          });
          if (data.success) {
            console.log("OOOOOO", data.data);
            localStorage.setItem("token", data.data.access_token);
            this.$store.dispatch("setUserDetails", {
              client_account_no: data.data.clientAccount.client_no,
              client_no : data.data.clientAccount.client_account_no,
              clientName: data.data.clientAccount.client_login_id,
              clientPersonName: data.data.clientAccount.client_person_name,
              clientCompanyName: data.data.clientAccount.client_name,
              icon_path: data.data.clientAccount.icon_path,
            });
            localStorage.setItem(
              "client_account_no",
              data.data.clientAccount.client_no
            );
            localStorage.setItem(
              "client_no",
              data.data.clientAccount.client_account_no
            );
            localStorage.setItem(
              "clientName",
              data.data.clientAccount.client_login_id
            );

            localStorage.setItem(
              "clientPersonName",
              data.data.clientAccount.client_person_name
            );
            localStorage.setItem(
              "clientCompanyName",
              data.data.clientAccount.client_name
            );
            localStorage.setItem(
              "icon_path",
              data.data.clientAccount.icon_path
            );
            this.$router.push({ name: "StoreData" });
          } else {
            this.loginValidation = this.$t("backend." + data.message.code);
          }
        } else {
          const { data } = await BasicInfoApis.postAPI(endPoint.MLOGIN, {
            client_login_id: this.login.login_id,
            client_login_pass: this.login.password,
          });
          if (data.success) {
            console.log("OOOOOO", data.data);
            localStorage.setItem("token", data.data.access_token);
            this.$store.dispatch("setUserDetails", {
              client_account_no: data.data.clientAccount.apl_manager_account_no,
              clientName: data.data.clientAccount.apl_manager_login_id,
              clientPersonName: data.data.clientAccount.client_person_name,
              clientCompanyName: data.data.clientAccount.person_company_name,
            });
            localStorage.setItem(
              "client_account_no",
              data.data.clientAccount.apl_manager_account_no
            );
            localStorage.setItem(
              "clientName",
              data.data.clientAccount.apl_manager_login_id
            );

            localStorage.setItem(
              "clientPersonName",
              data.data.clientAccount.client_person_name
            );
            localStorage.setItem(
              "clientCompanyName",
              data.data.clientAccount.person_company_name
            );
            localStorage.setItem(
              "personCompanyID",
              data.data.clientAccount.person_company_id
            );

            localStorage.setItem("personID", data.data.clientAccount.person_id);
            localStorage.setItem(
              "dummyData",
              data.data.access_control
                ? JSON.stringify(data.data.access_control)
                : {}
            );
            localStorage.setItem(
              "person_auth",
              data.data.clientAccount.person_auth_kbn
            );
            localStorage.setItem(
              "wiz_auth",
              data.data.clientAccount.wiz_auth_flg
            );
            localStorage.setItem(
              "supplier_auth",
              data.data.clientAccount.supplier_auth_flg
            );
            localStorage.setItem(
              "admin_auth",
              data.data.clientAccount.system_auth_flg
            );
            this.$router.push({ name: "admin_dasboard" });
          } else {
            this.loginValidation = this.$t("backend." + data.message.code);
          }
        }
      }
    },
    async getUserProfile() {
      try {
        const { data } = await BasicInfoApis.getAPI(endPoint.USER, "", true);
        if (data.success) {
          this.$store.dispatch("setUserDetails", data.data);
        } else {
          logValuesInSession("not_a_valid_user", "sorry not a valid user");
        }
      } catch (error) {
        logValuesInSession("user_details_profile", error);
      }
    },
    clearApiValidation() {
      this.loginValidation = "";
    },
  },
};
</script>
<style scoped lang="scss">
$primary: #062854 !default;
.login-btn {
  padding: 10px 50px 10px 50px !important;
  min-width: 200px !important;
  border-radius: 4px;
}
.bgColor {
  background-color: rgb(248, 248, 248);
}
.form-control {
  border-radius: inherit;
  height: 30px;
}
.txtColor {
  color: $primary;
  font-weight: 700 !important;
}
.linkTxt {
  color: var(--unnamed-color-003170);
  text-align: left;
  text-decoration: underline;
  font-size: 10px !important;
  letter-spacing: 0px;
  color: #003170;
  opacity: 1;
}
.footerDispaly {
  font: normal normal bold 12px/23px YuGothic;
  margin-top: 2rem;
  color: $primary;
}
.pc-view {
  background: var(--unnamed-color-ffffff) 0% 0% no-repeat padding-box;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 40px #0000000f;
  border-radius: 10px;
  opacity: 1;
}
.sp-view {
  background: #f7f7f7 0% 0% no-repeat padding-box;
  border-radius: 4px;
  opacity: 1;
  height: 40px !important;
  border: none !important;
}

@media (max-width: 991px) {
  .sp-view {
    font-size: 16px !important;
  }
}
</style>
